@use 'base/variables' as v;

.citas {
    list-style: none;
    padding: 0;
    li {
        padding: 1rem;
        border-top: 1px solid v.$blanco;
        span {
            font-weight: v.$bold;
            color: v.$azul;
        }

        .white {color: v.$blanco;}
        
        
    }
}


.servicios {@extend .citas;}